.input {
  background-color: #f0f0f0;
  border-radius: 0.5vw;
  border: none;
  height: 2vw;
  margin-top: 0.5vw;
  margin-bottom: 0vw;
  width: 20vw;
  text-rendering: auto;
  color: -internal-light-dark-color(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0.5vw;
  text-shadow: none;
  text-align: start;
  cursor: text;
  margin-left: 0.5vw;
  font: 1vw Arial;
  vertical-align: top;  
  align-items: center;

}

select {
  -webkit-writing-mode: horizontal-tb !important;
  text-rendering: auto;
  color: -internal-light-dark-color(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0.5vw;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  -webkit-appearance: menulist;
  box-sizing: border-box;
  align-items: center;
  white-space: pre;
  -webkit-rtl-ordering: logical;
  background-color: -internal-light-dark-color(
    rgb(255, 255, 255),
    rgb(59, 59, 59)
  );
  cursor: default;
  margin: 0em;
  font: 0.7vw Arial;
  border-radius: 0px;
  border-width: 1px;
  border-style: solid;
  border-color: -internal-light-dark-color(
    rgb(118, 118, 118),
    rgb(195, 195, 195)
  );
  border-image: initial;
  padding: 0vw;
}

.element.style {
  background-color: rgb(240, 240, 240);
  border-radius: 5px;
  border: none;
  height: 2vw;
  width: 15vw;
  font-size: 1vw;
  padding: 0vw;
  background-color: white;
  text-indent: 1vw;
  display: flex !important;
  vertical-align: top !important;  
}

.form-control-plaintext {
  display: block;
  width: 100%;
  padding: 0vw;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.5;
  color: #212529;
  background-color: transparent;
  border: solid transparent;
  border-width: 1px 0;
}

.form-control-plaintext {
  display: block;
  width: 100%;
  padding: 0vw;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.5;
  color: #212529;
  background-color: transparent;
  border: solid transparent;
  border-width: 1px 0;
  text-indent: 0.5vw;
}

.react-datepicker__year-read-view--down-arrow,
.react-datepicker__month-read-view--down-arrow,
.react-datepicker__month-year-read-view--down-arrow {
  border-top-color: #ccc;
  float: right;
  margin-left: 1.75vw;
  top: 0.15vw;
  position: relative;
  border-width: 0.45rem;
}

.react-datepicker__month-container {
  float: left;
  width: 25vw;
  height: 18vw;
  line-height: 0.75vw;
}
